export default "export const keyCode = {\r\n    backspace: 8,\r\n    tab: 9,\r\n    enter: 13,\r\n    shift: 16,\r\n    ctrl: 17,\r\n    alt: 18,\r\n    pause: 19,\r\n    capsLock: 20,\r\n    escape: 27,\r\n    pageUp: 33,\r\n    pageDown: 34,\r\n    end: 35,\r\n    home: 36,\r\n    leftArrow: 37,\r\n    upArrow: 38,\r\n    rightArrow: 39,\r\n    downArrow: 40,\r\n    insert: 45,\r\n    delete: 46,\r\n};\r\n\r\n/**\r\n * taken from https://stackoverflow.com/a/7394787\r\n */\r\nexport const decodeHtml = (html: string) => {\r\n    const txt = document.createElement('textarea');\r\n    txt.innerHTML = html;\r\n    return txt.value;\r\n};\r\n"